@font-face {
	font-family: "RobotoBold";
	src: url("../../fonts/Roboto/Roboto-Bold.ttf");

			
	font-style: normal;
	font-weight: normal;
}
@font-face {
	font-family: "RobotoItalic";
	src: url("../../fonts/Roboto/Roboto-Italic.ttf");

			
	font-style: normal;
	font-weight: normal;
}
@font-face {
	font-family: "RobotoMedium";
	src: url("../../fonts/Roboto/Roboto-Medium.ttf");

			
	font-style: normal;
	font-weight: normal;
}
.searchBoard{
    height: 100%;
    
   background: #6937FA;
   
    .searchItems{
        display: flex;
        flex-direction: column;
        height: 970px;
        
        background: url(../../img/site/searchBar_background.png) no-repeat;
        background-position: bottom,center;
        h1{
            position: relative;
            top: -15px;
            letter-spacing: 10px;
            margin-left: 140px;
            font-size: 80px;
            line-height: 100px;
            color: white;
            font-family: "RobotoBold";
            width: 1000px;
        }
        input{
           position: relative;
           left: 40px;
           top: -90px;
            padding: 0;
            margin: 0;
            width: 790px;
            height: 80px;
            border: none;
            border-radius: 40px;
            outline:none;
            font-family: "RobotoItalic";
            padding-left: 100px;
            font-size: 34px;
        }
        button{
            position: relative;
            top: -95px;
            left: 8px;
            height: 80px;
            width: 195px;
            background: #EC971F;
            border: none;
            border-radius: 40px;
            font-size: 25px;
            font-family: "RobotoMedium";
            padding: 0;
            margin: 0;
            margin-left: 5%;
            padding-bottom: 15px;
            color: white;
            img{
                position: relative;
                top: 10px;
                left: -10px;
            }
        }
        .inputBlock{
            margin-left: 5%;
            margin-top: 100px;
        }
    }
}