@font-face {
  font-family: "RobotoRegular";
  src: url("../../fonts/Roboto/Roboto-Regular.ttf");

  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "RobotoItalic";
  src: url("../../fonts/Roboto/Roboto-Italic.ttf");

  font-style: normal;
  font-weight: normal;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  height: 155px;
  background: #6937fa;
  img {
  
    margin-left: 140px;
    width: 275px;
    height: 85px;
  }
  button {
    font-size: 22px;
    font-family: "RobotoRegular";
    
    color: white;
    background: none;
    border: none;
    cursor: pointer;
  }
  .buttGroup {
    
    width: 565px;
    display: flex;
    justify-content: space-between;
  }
  .loginBlock {
    a{
      font-size: 22px;
    font-family: "RobotoRegular";
    
    color: white;
    background: none;
    border: none;
    cursor: pointer;
    }
    button{
      padding-left: 25px;
     
    }
    display: flex;
  
  
    margin-right: 140px;
  }
  .searchBar {
    display: flex;
  
    button {
      height: 53px;
      width: 165px;
      background: #ec971f;
      border: none;
      border-top-right-radius: 10px;
      border-bottom-right-radius: 10px;

      padding: 0;
      margin: 0;
    }
    input {
      padding: 0;
      margin: 0;
      padding-left: 50px;
      width: 485px;
      height: 53px;
      border: none;
      border-top-left-radius: 10px;
      border-bottom-left-radius: 10px;
      font-family: "RobotoItalic";
      outline:none;
    }
    input:active {
      border: none;
    }
  }
}
.hide {
  display: none;
}
.popUp {
  .popUpElements {
    z-index: 4;
    position: absolute;
    left: 25%;
  }
  .shadow {
    height: 900px;

    position: fixed;
    z-index: 3;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba($color: #000000, $alpha: 0.6);
  }
  .categoriesSelect {
    width: 870px;
    height: 700px;
    overflow: hidden;
    border-radius: 40px;
    background: white;
    display: flex;

    .categories {
      text-align: center;
      width: 50%;
      background: white;
      color: #6937fa;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      button {
        width: 245px;
        height: 45px;
        border: none;
        background: #6937fa;
        border-radius: 10px;
        font-size: 22px;
        color: white;
        margin-bottom: 100px;
        margin-left: 100px;

        cursor: pointer;
      }
      ul {
        margin: 0;
        padding: 0;
        list-style-type: none;
        text-align: left;

        li {
          padding-left: 20px;
          cursor: pointer;
          font-size: 22px;
          font-family: "RobotoRegular";
          margin-top: 15px;
        }
        li:hover {
          cursor: pointer;
          font-size: 22px;
          font-family: "RobotoRegular";
          margin-top: 15px;
          color: white;
          background: #6937fa;
        }
        .selectedInd {
          color: white;
          background: #6937fa;
        }
      }

      h3 {
        font-size: 36px;
      }
    }
    .specialization {
      text-align: center;
      width: 50%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      color: white;
     
      .specList {
        height: 400px;
        overflow-y: scroll;
        &::-webkit-scrollbar {
          width: 0.5em;
          height: 0.5em;
         }
       
         &::-webkit-scrollbar-thumb {
          background-color: rgba(241, 236, 236, 0.4);
          border-radius: 3px;
       
          
         }
        
      }
      .selectedSpec {
        color: #6937fa;
        background: white;
      }
      h3 {
        font-size: 36px;
      }
      ul {
        padding: 0;
        list-style-type: none;
        text-align: center;
        li {
          cursor: pointer;
          width: 100%;
          font-size: 22px;
          font-family: "RobotoRegular";
          margin-top: 15px;
        }
        li:hover {
          background: white;
          color: #6937fa;
        }
      }
      button {
        width: 245px;
        height: 45px;
        border: none;
        background: white;
        border-radius: 10px;
        font-size: 22px;
        color: #6937fa;
        cursor: pointer;
        margin-bottom: 100px;
        margin-left: 100px;
      }
      width: 50%;
      background: #6937fa;
    }
  }
  .logInPopUp {
    width: 870px;
    height: 700px;
    overflow: hidden;
    border-radius: 40px;
    background: white;
    display: grid;
    grid-template-columns: 45% 55%;
    background: #6937fa;
    justify-content: center;
    .errorSpan {
      color: red;
      margin: 0;
      padding: 0;
      font-size: 15px;
    }
    .firstSignUpBlock {
      background: #6937fa;
      position: relative;
      right: -15px;
      display: flex;
      justify-content: center;

      .firstSignUpBlock_content {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        color: white;
        font-family: "RobotoRegular";
        width: 293px;
        height: 490px;
        font-size: 20px;
        h3 {
        }
        p {
          font-size: 26px;
          width: 269px;
        }
        h4 {
        }
        span {
          cursor: pointer;

          color: #ec971f;
        }
      }
    }
    .secondSignUpBlock_content {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      color: #6937fa;
      font-family: "RobotoRegular";
      width: 293px;
      height: 490px;
      font-size: 20px;
      .passBlock {
        width: 293px;
      }
    }
    .emailBlock {
      width: 293px;
    }
    .secondSignUpBlock {
      border-radius: 27px;
      position: relative;
      z-index: 10;
      background: white;
      display: flex;
      justify-content: center;
      .passBlock {
        span {
          font-size: 18px;
          width: 100%;
          color: #a6a6a6;
          cursor: pointer;
        }
      }

      button {
        background: #6937fa;
        width: 300px;
        height: 40px;
        border: none;
        border-radius: 8px;
        color: white;
        font-size: 20px;
        cursor: pointer;
      }
    }
  }
}
