@font-face {
  font-family: "RobotoRegular";
  src: url("../../fonts/Roboto/Roboto-Regular.ttf");

  font-style: normal;
  font-weight: normal;
}
.footerConteiner{
  
}
.footer {
  background: #6937fa;
  height: 480px;
  display: grid;
  grid-template-columns: 25% 19% 16% 15% 25%;
  grid-template-rows: 50% 50%;
 .allRight{
   margin-left: 30px;
 }
 .toTop{
  display: flex;
  justify-content: center;
  align-items: center;
  a{
    border: none;
    width: 53px;
    height: 53px;
    padding: 0;
    margin: 0;
    margin-left: 300px;
    img{
      width: 53px;
      height: 53px;
    }
  }
}
  .logoBlock {
    display: flex;
    align-items: center;
    a{
      border: 2px white solid;
      border-radius: 30px;
      font-size: 22px;
      font-family: "RobotoRegular";
      color: white;
      
      padding: 20px 40px;
    }
    img {
      margin-left: 70px;
      width: 230px;
      height: 84px;
      margin-right: 60px;
    }
    .socBlock{
      width: 263px;
      display: flex;
      
      img{
        margin: 0;
        margin-right: 18px;
   width: 53px;
   height: 53px;
      }
     
    }
   
    
  }
  h5 {

    color: white;
    font-size: 20px;
    font-family: "RobotoRegular";
  }
  
  h4 {
    font-size: 30px;
    text-decoration: underline;
    color: white;
    font-weight: 700;
    font-family: "RobotoRegular";
  }
  ul {
    text-align: left;
    padding-inline-start: 25px;
    li {
      margin: 0;
      color: white;
      font-size: 20px;
      font-family: "RobotoRegular";
    }
  }
  .contactUs {
    ul {
      list-style-type: none;
      padding-inline-start: 5px;
      font-family: "RobotoRegular";
    }
  }
}
.info {
  display: flex;
  margin-left: 5%;

  align-items: center;
  margin-right: 5%;
}
