@font-face {
  font-family: "RobotoBlack";
  src: url("../../fonts/Roboto/Roboto-Black.ttf");

  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "RobotoMedium";
  src: url("../../fonts/Roboto/Roboto-Medium.ttf");

  font-style: normal;
  font-weight: normal;
}
.searchPage_container {
  .searchPage_subContainer {
    display: flex;
  }
  h4{
    font-family: "RobotoMedium";
    font-size: 22px;
    color: #8f8f8f;
  }
  .profileContainer {
    min-width: 288px;
    margin-top: 50px;
    margin-left: 40px;
    border-radius: 25px;
    height: 376px;

    overflow: hidden;

    .profileBlock {
      background: white;
     width: 288px;
      display: flex;
      h5{
        margin-left: 20px;
        font-family: "RobotoMedium";
    font-size: 26px;
    color: #2F74F3;
      }
      .profileInfo_img {
        width: 121px;
        height: 121px;
        border-radius: 50%;
        overflow: hidden;
        img {
          width: 121px;
          height: 121px;
        }
      }
    }
    .profileInfo {
      display: flex;
      flex-direction: column;
      align-items: center;
      min-width: 288px;
      height: 306px;
      text-align: left;
      .skillAndVotesCount{
        display: flex;
        img{
        width: 20px;
        max-height: 20px;
        margin-right: 15px;
        }
      }
      h5,
      h6 {
        color: #2f74f3;
        margin: 0;
        padding: 0;
        font-size: 26px;
        font-family: "RobotoMedium";
      }
      h5 {
        margin-top: 25px;
        margin-bottom: 15px;
      }
    }
    .buttBlock {
      button {
        min-width: 288px;
        border: none;
        cursor: pointer;
        margin-top: 5px;
        height: 68px;
        background: #ec971f;
        color: white;
        font-size: 26px;
        font-family: "RobotoMedium";
        border-bottom-left-radius: 25px;
        border-bottom-right-radius: 25px;
      }
    }
  }
  h3 {
    font-family: "RobotoBlack";
    font-size: 30px;
    color: #8f8f8f;
  }
  margin-left: 140px;
  margin-right: 140px;
  .first {
    margin-top: 50px;
    border-top-right-radius: 34px;
    border-top-left-radius: 34px;
  }
  .last {
    border-bottom-right-radius: 34px;
    border-bottom-left-radius: 34px;
  }
  .searchResult {
    transition: 1s;

    overflow: hidden;
    margin-bottom: 10px;
    background: white;

    display: flex;
    justify-content: space-between;

    .searchResult_img {
      display: flex;
      img {
        margin-top: 30px;
        margin-left: 30px;
        width: 200px;
        height: 200px;
      }
    }
    .searchResult_firstBlock {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-left: 60px;
    }
    h1 {
      font-size: 36px;
      font-family: "RobotoMedium";
      color: #707070;
      margin-bottom: 0px;
    }
    p {
      margin-left: 30px;
      margin-bottom: 50px;
      font-size: 26px;
      font-family: "RobotoBlack";
      color: #707070;
    }

    button {
      background: #e2dddd;
      margin-bottom: 30px;
      border: none;
      border-radius: 10px;
      min-width: 170px;
      max-width: 400px;
      height: 40px;
      font-size: 22px;
      font-family: "RobotoMedium";

      a {
        color: #707070;
      }
    }
    .searchResult_secondBlock {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: flex-end;
      margin-right: 50px;

      span {
        font-family: "RobotoMedium";
        font-size: 26px;
        color: #8f8f8f;
      }
      .voteCount {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        img {
          width: 30px;
          height: 30px;
        }
        border: 1px #707070 solid;
        width: 75px;
        height: 85px;
        border-radius: 10px;
        margin-bottom: 55px;
        font-family: "RobotoMedium";
        font-size: 26px;
        color: #8f8f8f;
      }
      .active{
        border: 3px green solid;
      }
    }
  }
  .searchResult:hover {
    transition: 1s;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.6);
  }
}
