
@import "icons";
@import "metismenu";

@import url("https://fonts.googleapis.com/css?family=Hind:400,500,700|Noto+Sans:400,700");
/* Metis Menu css */
.metismenu {
  padding: 0;
  list-style: none;
  margin: 0;
}

.metismenu ul {
  padding: 0;
  margin: 0;
}

.metismenu ul li {
  list-style: none;
}

.nav-second-level li a, .nav-thrid-level li a {
  padding: 8px 20px 8px 10px;
  color: rgba(98, 103, 115, 0.7);
  display: block;
  position: relative;
  font-size: 14px;
}

.nav-second-level li a:focus, .nav-thrid-level li a:focus {
  background-color: #ffffff;
  color: #626773;
}

.nav-second-level li a:hover, .nav-thrid-level li a:hover {
  background-color: #ffffff;
  color: #626773;
}

.nav-second-level > li > a {
  padding-left: 58px;
}

.nav-second-level li.active > a {
  color: #626773;
  background-color: #ffffff;
}

.nav-third-level > li > a {
  padding-left: 68px;
}

.nav-third-level li.active > a {
  color: #626773;
}

.user-details {
  min-height: 80px;
  padding: 20px;
  position: relative;
}

.user-details img {
  position: relative;
  z-index: 9999;
  height: 48px;
  width: 48px;
}

.user-details .user-info {
  color: #626773;
  margin-left: 60px;
  position: relative;
  z-index: 99999;
}

.user-details .user-info p {
  margin-bottom: 0;
  font-size: 13px;
}

.user-details .user-info a {
  color: #626773;
  display: block;
  font-weight: 600;
  padding-top: 5px;
}

.topbar {
  left: 0;
  right: 0;
  top: 0;
  z-index: 999;
  position: relative;
  background: #fff;
  -webkit-box-shadow: 0 0 18px 0 rgba(0, 0, 0, 0.3);
  box-shadow: 0 0 18px 0 rgba(0, 0, 0, 0.3);
}

.topbar .topbar-left {
  float: left;
  padding-left: 20px;
  height: 70px;
  position: relative;
  width: 400px;
  z-index: 999;
}

.topbar .topbar-left .logo {
  line-height: 70px;
}

.topbar .topbar-left .logo i {
  display: none;
}

.topbar .topbar-left .logo img {
  height: 35px;
}

.navbar-custom {
  border-radius: 0;
  margin-bottom: 0;
  padding: 0 10px 0 0;
  margin-left: 240px;
  min-height: 70px;
}

.navbar-custom .nav-link {
  padding: 0;
  line-height: 70px;
  color: #666;
}

.navbar-custom .dropdown-toggle:after {
  content: initial;
}

.navbar-custom .menu-left {
  overflow: hidden;
}

.navbar-custom .topbar-right-menu {
  max-height: 70px;
}

.navbar-custom .topbar-right-menu li {
  float: left;
}

.logo {
  color: #626773 !important;
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-right: 20px;
}

.logo span span {
  color: #23b195;
}

.user-box {
  text-align: center;
  padding: 30px 0 20px 0;
}

.user-box .user-img {
  position: relative;
  height: 88px;
  width: 88px;
  margin: 0 auto;
}

.user-box h5 a {
  color: #626773;
}

.side-menu {
  position: absolute;
  background-color: #ffffff;
  width: 300px;
  top: 70px;
  -webkit-transition: margin-left 0.3s;
  transition: margin-left 0.3s;
  background-repeat: repeat;
  z-index: 99;
}

.enlarged .side-menu {
  position: absolute;
}

.content-page {
  margin-left: 300px;
  overflow: hidden;
  border-left: 7px solid #f5f5f5;
}

.content-page .content {
  padding: 0 10px 50px 10px;
  margin-top: 20px;
  min-height: 1040px;
  position: relative;
}

.button-menu-mobile {
  border: none;
  color: #666;
  display: inline-block;
  height: 70px;
  width: 60px;
  background-color: transparent;
  font-size: 24px;
  z-index: 999;
  position: relative;
  cursor: pointer;
}

#sidebar-menu > ul > li > a {
  color: rgba(98, 103, 115, 0.8);
  display: block;
  padding: 12px 20px;
  margin: 2px 0;
  font-size: 14px;
  position: relative;
}

#sidebar-menu > ul > li > a:hover, #sidebar-menu > ul > li > a:focus, #sidebar-menu > ul > li > a:active {
  color: #626773;
  text-decoration: none;
  background-color: whitesmoke;
}

#sidebar-menu > ul > li > a > span {
  vertical-align: middle;
}

#sidebar-menu {
  padding-top: 10px;
}

#sidebar-menu .menu-arrow {
  -webkit-transition: -webkit-transform .15s;
  transition: -webkit-transform .15s;
  transition: transform .15s;
  transition: transform .15s, -webkit-transform .15s;
  position: absolute;
  right: 20px;
  display: inline-block;
  font-family: 'Material Design Icons';
  text-rendering: auto;
  line-height: 28px;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
}

#sidebar-menu .menu-arrow:before {
  content: "\F142";
}

#sidebar-menu .badge, #sidebar-menu .label {
  margin-top: 4px;
}

#sidebar-menu li.active .menu-arrow {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

#sidebar-menu ul li a i {
  display: inline-block;
  font-size: 18px;
  line-height: 17px;
  margin: 0 10px 0 3px;
  text-align: center;
  vertical-align: middle;
  width: 20px;
}

#sidebar-menu ul li a .drop-arrow {
  float: right;
}

#sidebar-menu ul li a .drop-arrow i {
  margin-right: 0;
}

#sidebar-menu > ul > li > a.active {
  color: #626773 !important;
  background-color: whitesmoke;
}

.menu-title {
  padding: 12px 20px !important;
  letter-spacing: .05em;
  pointer-events: none;
  cursor: default;
  font-size: 11px;
  text-transform: uppercase;
  color: #7a7d84;
  font-family: "Hind", sans-serif;
}

.enlarged .slimScrollDiv, .enlarged .slimscroll-menu {
  overflow: inherit !important;
}

.enlarged .slimScrollBar {
  visibility: hidden;
}

.enlarged #wrapper .navbar-custom {
  margin-left: 70px;
}

.enlarged #wrapper #sidebar-menu .menu-title, .enlarged #wrapper #sidebar-menu .menu-arrow, .enlarged #wrapper #sidebar-menu .label, .enlarged #wrapper #sidebar-menu .badge {
  display: none !important;
}

.enlarged #wrapper #sidebar-menu .collapse.in {
  display: none !important;
}

.enlarged #wrapper #sidebar-menu .nav.collapse {
  height: inherit !important;
}

.enlarged #wrapper #sidebar-menu ul ul {
  margin-top: -2px;
  padding-bottom: 5px;
  padding-top: 5px;
  z-index: 9999;
  background-color: #f5f5f5;
  height: auto !important;
}

.enlarged #wrapper .left.side-menu {
  width: 70px;
  z-index: 5;
}

.enlarged #wrapper .left.side-menu #sidebar-menu > ul > li > a {
  padding: 15px 20px;
  min-height: 56px;
}

.enlarged #wrapper .left.side-menu #sidebar-menu > ul > li > a:hover, .enlarged #wrapper .left.side-menu #sidebar-menu > ul > li > a:active, .enlarged #wrapper .left.side-menu #sidebar-menu > ul > li > a:focus {
  color: #626773 !important;
  background-color: whitesmoke;
}

.enlarged #wrapper .left.side-menu #sidebar-menu > ul > li > a i {
  font-size: 18px;
  margin-right: 20px !important;
}

.enlarged #wrapper .left.side-menu .label {
  position: absolute;
  top: 5px;
  left: 35px;
  text-indent: 0;
  display: block !important;
  padding: .2em .6em .3em !important;
}

.enlarged #wrapper .left.side-menu #sidebar-menu ul > li {
  position: relative;
  white-space: nowrap;
}

.enlarged #wrapper .left.side-menu #sidebar-menu ul > li:hover > a {
  position: relative;
  width: 260px;
  color: #626773 !important;
  background-color: whitesmoke;
}

.enlarged #wrapper .left.side-menu #sidebar-menu ul > li:hover > ul {
  display: block;
  left: 70px;
  position: absolute;
  width: 190px;
}

.enlarged #wrapper .left.side-menu #sidebar-menu ul > li:hover > ul a {
  -webkit-box-shadow: none;
  box-shadow: none;
  padding: 8px 20px;
  position: relative;
  width: 190px;
  z-index: 6;
}

.enlarged #wrapper .left.side-menu #sidebar-menu ul > li:hover > ul a:hover {
  color: #626773;
}

.enlarged #wrapper .left.side-menu #sidebar-menu ul > li:hover a span {
  display: inline;
}

.enlarged #wrapper .left.side-menu #sidebar-menu ul ul li:hover > ul {
  display: block;
  left: 190px;
  margin-top: -36px;
  position: absolute;
  width: 190px;
}

.enlarged #wrapper .left.side-menu #sidebar-menu ul ul li > a span.pull-right {
  -ms-transform: rotate(270deg);
  -webkit-transform: rotate(270deg);
  position: absolute;
  right: 20px;
  top: 12px;
  transform: rotate(270deg);
}

.enlarged #wrapper .left.side-menu #sidebar-menu ul ul li.active a {
  color: #626773;
}

.enlarged #wrapper .left.side-menu #sidebar-menu ul > li > a span {
  display: none;
  padding-left: 10px;
}

.enlarged #wrapper .left.side-menu .user-details {
  display: none;
}

.enlarged #wrapper .content-page {
  margin-left: 70px;
}

.enlarged #wrapper .footer {
  left: 70px;
}

.enlarged #wrapper .topbar .topbar-left {
  width: 70px !important;
}

.enlarged #wrapper .topbar .topbar-left .logo span {
  display: none;
  opacity: 0;
}

.enlarged #wrapper .topbar .topbar-left .logo i {
  display: block;
  line-height: 70px;
  color: #23b195 !important;
}

.enlarged #wrapper #sidebar-menu > ul > li:hover > a.open :after {
  display: none;
}

.enlarged #wrapper #sidebar-menu > ul > li:hover > a.active :after {
  display: none;
}

#wrapper.right-bar-enabled .right-bar {
  right: 0;
}

