@font-face {
  font-family: "RobotoBlack";
  src: url("../../../fonts/Roboto/Roboto-Black.ttf");

  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../../../fonts/Roboto/Roboto-Medium.ttf");

  font-style: normal;
  font-weight: normal;
}
.wrapper {
  background: #e4e4e4;
}
.container {
  .firstBlock {
    text-align: center;
    

    margin-top: 130px;
    
    height: 2520px;
    border-radius: 130px;
    margin-left: 140px;
    margin-right: 140px;
    background: white;
    padding-top: 65px;
    h3 {

      color: #2f74f3;
      font-size: 60px;
      font-family: "RobotoBlack";
    }
    .industriesGroup {
      margin-top: 140px;
      margin-left: 65px;
      display: grid;
      grid-template-columns: 31% 38% 31%;
      grid-template-rows: 415px 415px 415px 415px;
      align-items: end;
      margin-bottom: 100px;
      h4 {
        margin: 0;
        padding: 0;
        color: #2f74f3;
        font-size: 38px;
        font-family: "RobotoBlack";
      }
      button {
        margin-top: 30px;
        width: 225px;
        height: 40px;
        background: #ec971f;
        border: none;
        color: white;
        font-family: "Roboto";
        font-size: 18px;
        border-radius: 15px;
      }
    }
    
    
    .first,
    .third,
    .second,.first-active,.second-active,.third-active {
        transition: 1s;
        background: white;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
      margin-top: 50px;
      img{
       
          margin-top: 40px;
        
      }
    }
    .second-active{
        height: 319px;
        border-radius: 20px;
        img {
          transition: 1s;
          height: 128px;
          width: 128px;
          
        }
    }
    .first,
    .third {
        background: white;
      img {
        transition: 1s;
        height: 128px;
        width: 128px;
       
      }
    }
    .second {
      border-radius: 20px;
      height: 415px;
      background: white;
      z-index: 1;
      
      img {
        transition: 1s;
        height: 170px;
        width: 170px;
        
      }
    }
    .first {
      border-radius: 25px;
      position: relative;
      right: -15px;
      height: 319px;
    
    }
    .third {
        border-radius: 25px;
      position: relative;
      left: -15px;
      height: 319px;
     
    }

    .first-active {
        
      position: relative;
      right: -15px;
      height: 415px;
      z-index: 2;
      border-radius: 25px;
      img {
        transition: 1s;
        height: 170px;
        width: 170px;
        
      }
    }
   .third-active{
    border-radius: 25px;
    position: relative;
    left: -15px;
    height: 415px;
    img {
      transition: 1s;
      height: 170px;
      width: 170px;
      
    }
   }
  }
  .secondBlock,
  .thirdBlock {
    margin-top:105px;
  
    height: 600px;
    border-radius: 50px;
    margin-left: 70px;
    margin-right: 70px;
    background: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
    h5 {
      width: 550px;
      font-size: 55px;
      font-family: "RobotoBlack";
      color: #2f74f3;
    }
    img {
      height: 370px;
      width: 620px;
    }
  }
  .thirdBlock {
    margin-bottom: 90px;
  }
}
